			//选择地理位置 ,进行导航
			uni.getLocation({
				type: 'gcj02',
				success: function(res) {
					console.log(res)

					if (res) {
						uni.openLocation({
							latitude: res.latitude,
							longitude: res.longitude,
							name: res.name,
