					console.log(res)
					if (res.code) {
						let code = res.code
						_self.$http.request({
							url: '/api/login/getAlipayToken',
							type: 'POST',
							data: {
								code
							},
							success(res) {
								console.log(res)
								let userId = res.data.userId
								_self.$api.setStorageSync('userId', userId)

							}
						})

					}
				}
			})
